.wrapper {
    background-color: $offwhite;
    height: calc(100vh - 80px);
    // height: 100%;
    position: absolute;
    left: -80px;
    display: flex;
    flex-direction: row;
    width: fit-content;
    transform: rotate(90deg) translateY(-100vh);
    transform-origin: top left;
    padding-left: 22px;
    
    @import "titleCard.scss";
    @import "img.scss";
    @import "card.scss";
    @import "cardWork.scss";
    @import "textArea.scss";
    @import "endCard.scss";
}

@media only screen and (max-width: 916px) {
    .wrapper {
    // height: calc(100vh - 80px);
    height: auto;
    position: static;
    left: 0;
    display: flex;
    flex-direction: column;
    width: 100vw;
    transform: rotate(0) translateY(0);
    transform-origin: top left;
    padding: 0;
    
    @import "titleCard.scss";
    @import "img.scss";
    @import "card.scss";
    @import "cardWork.scss";
    @import "textArea.scss";
    @import "endCard.scss";
}
}

