header {
    width: 100vw;
    height: 80px;
    background-color: $white;
    padding-left: 22px;
    padding-right: 22px;
    display: flex;
    justify-content: space-between;
    align-items: center;

    .logo {
        display: flex;
        justify-content: Left;
        padding-left: 158px;

        h1 {
            margin: 0;
            font-size: 35px;
            display: block;

            a {
                color: $black;
            }

            a:hover {
                text-decoration: none;
                color: $black;
            }
        }
    }

    ul {
        display: flex;
        justify-content: end;
        padding-right: 158px;
        list-style: none;
        flex-direction: row;
        margin: 0;

        div {
            padding: 0;
            width: 70px;
            height: 80px;
            display: flex;
            justify-content: center;
            align-items: center;
            // background-color: $lightgrey;
            margin-left: 80px;

            li a {
                display: inline;
                font-family: Arial, Helvetica, sans-serif;
                color: $grey;
                transition-duration: 0.2s;
                font-size: 18px;

                &:hover {
                    text-decoration: none;
                    color: $black;
                    transition-duration: 0.2s;
                }
            }

            .navActive {
                font-weight: bold;
                color: $black;
            }
        }
    }
}

@media only screen and (max-width: 916px) {
    header {
        padding: 0;
        width: 100vw;
        
        ul {
            display: none;
        }
    }
}
