.imgBG{
    position: relative;
}

.imgCon{
    height: 100%;
    padding-top: 110px;
    padding-bottom: 110px;
    margin-right: 178px;
    position: relative;

    display: flex;
    // align-items: center;
    justify-content: center;
    overflow: hidden;

    img {
        min-width: 100%;
        min-height: 100%;
        display: block;
        object-fit: cover;
    }
}

@media only screen and (max-width: 916px) {
    .imgCon{
        height: 450px;
        padding-top: 0;
        padding-bottom: 110px;
        margin-right: 0;
        position: relative;
    
        display: flex;
        // align-items: center;
        justify-content: center;
        overflow: hidden;
    }
}