.endCard{
    padding-top: 0px;
    padding-bottom: 0px;
    padding-left: 158px;
    padding-right: 158px;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    background-color: $lightgrey;
    height: 100%;

    h1{
        font-size: 50px;
        margin-bottom: -10px;
    }

    a{
        font-family: 'Courier', Courier, monospace;
        padding-right: 10px;
        line-height: 1.5em;
        font-size: 22px;
        color: $grey;
        transition-duration: 0.2s;

        // &:before{
        //     content: "→";
        //     padding-right: 10px;
        //     text-decoration: none;
        // }

        &:hover{
            color: $black;
            text-decoration: underline;
            transition-duration: 0.2s;
        }
    }
        
    p{
        max-height: 320px;
        margin: 0;
        color: $grey;

    }
    
    .footer{
        text-align: right;
    }

    @import "contactForm.scss";

}

@media only screen and (max-width: 916px) {
    .endCard {
        margin: 0;
        // padding: 0;
        width: 100vw;
    }
}